import React from 'react';


const Navigation = () => (
    <ul className="navigation__list">
        <a className="navigation__button" href={window.location.hostname}><li className="navigation__item">blog</li></a>
        <a className="navigation__button" href={window.location.hostname + '/gallery'}><li className="navigation__item">galeria</li></a>
    </ul>
)

export default Navigation